* {
    font-family: 'Montserrat', sans-serif;
    padding: 0;
    margin: 0;
}
.overlay {
     position: absolute;
     top: 0;
     bottom: 0;
     left: 0;
     right: 0;
     background-color: rgba(0, 0, 0, 0.7);
 }

.modalForm {
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 395px;

    z-index: 3;
    background-color: #FFFFFF;
    border-radius: 2px;


}

.titleBlock {
    display: flex;

    justify-content: space-between;
    align-items: center;
    padding: 19px 24px;
    font-style: normal;
    font-weight: 500;
    font-size: 18px;
    line-height: 22px;
    min-height: max-content;


}
.contentChildren {
    padding: 0 24px 36px 24px;
    border-top: solid 1px  #D9D9D9;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
 gap: 20px;
    padding-top: 10px;
}

.buttonsBlock {
    display: flex;
    justify-content: space-around;
}

.input {
    width: 150px;
}